import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Search from './pages/Search';
import Select from './pages/Select';
import LifeGame from './pages/LifeGame'

export default function App() {
  return (
    <Router>
        <Switch>
          <Route component={LifeGame} path="/conway"></Route>
          <Route component={Search} path="/pokemon_find"></Route>
          <Route component={Select} path="/pokemon_cal"></Route>
          <Route component={Home} path="/"></Route>
        </Switch>
    </Router>
  );
}

function Home() {
  return <div>
    <div>
      <Link to="/conway">生命游戏</Link>
    </div>
    <div>
      <Link to="/pokemon_cal">宝可梦属性克制查询</Link>
    </div>
    <div>
      <Link to="/pokemon_find">宝可梦属性查询名称检索</Link>
    </div>
  </div>
}